<template>
  <div>
    <div v-if="lists.length" id="buycar">
      <table>
        <tr>
          <th></th>

          <th></th>

          <th></th>

          <th>/</th>

          <th></th>

          <th></th>

          <th></th>

          <th></th>

          <th></th>
        </tr>

        <tr v-for="list in lists" :key="list.id">
          <td>
            <el-checkbox
              v-model="seletedArrCloth"
              :label="list.id"
              @change="list.checked = !list.checked"
            ></el-checkbox>
          </td>

          <td>
            <img :src="list.image" />
          </td>

          <td>
            <span>{{list.name}}</span>
          </td>

          <td>{{list.size}}/{{list.color}}</td>

          <td>{{list.price}}</td>

          <td class="col">
            <span @click="addCount(list)" class="span">+</span>
            {{list.count}}
            <span @click="CutCount(list)" class="span">-</span>
          </td>

          <td>{{list.active}}</td>

          <td>{{list.price*list.count}}</td>

          <td>
            <img class="delIcon" @click="delIcon(list)" src="../../static/image/delIcon.png" />
          </td>
        </tr>
      </table>
    </div>

    <div v-else>^-^</div>

    <Footer
      id="Footer"
      :lists="lists"
      :totalPrice="totalPrice"
      :totalCount="totalCount"
      :seletedArrCloth="seletedArrCloth"
      @funct="getMessage"
      :allchecked="allchecked"
    ></Footer>
  </div>
</template>
<script>
import Footer from "../components/footer.vue";
export default {
  name: "buycar",
  data() {
    return {
      seletedArrCloth: [],
      lists: [],
      totalPrice: "",
      totalCount: "",
      allchecked: "",
    };
  },
  created() {
    this.getLists();
    this.allchecked = false;
  },
  watch: {
    seletedArrCloth() {
      this.getTotalPrice();
      this.getTotalCount();
      if (this.seletedArrCloth.length == this.lists.length) {
          console.log(this.seletedArrCloth)
        this.allchecked = true;
      } else {
        this.allchecked = false;
      }
    }
  },
  methods: {
    getTotalPrice() {
      let totalPrice = 0;
      this.lists.forEach(function(list) {
        if (list.checked) {
          console.log(list.price);
          totalPrice =
            totalPrice +
            parseFloat(list.price).toFixed(2) * list.active * list.count;
        }
      });
      this.totalPrice = totalPrice;
    },
    getTotalCount() {
      let totalCount = 0;
      this.lists.forEach(function(list) {
        if (list.checked) {
          console.log(list.totalCount);
          totalCount = totalCount + parseInt(list.count);
        }
      });
      this.totalCount = totalCount;
    },
    getLists() {
      //ajax
      let arr = [
        {
          image: "../../static/image/redcloth.png",
          name:
            " J19329M",
          count: "2",
          size: "M",
          color: "",
          price: "100",
          active: "0.5",
          id: "1"
        },
        {
          image: "../../static/image/cloth2.png",
          name:
            " J19329M",
          count: "2",
          size: "M",
          color: "",
          price: "100",
          active: "0.5",
          id: "2"
        },
        {
          image: "../../static/image/blackcloth.jpg",
          name:
            " J19329 M",
          count: "2",
          size: "M",
          color: "",
          price: "100",
          active: "0.5",
          id: "3"
        },
        {
          image: "../../static/image/pinkcloth.jpg",
          name:
            "  051 M",
          count: "2",
          size: "M",
          color: "",
          price: "100",
          active: "0.5",
          id: "4"
        },
        {
          image: "../../static/image/cloth2.png",
          name:
            " J19329 XL",
          count: "2",
          size: "M",
          color: "",
          price: "100",
          active: "0.5",
          id: "5"
        },
        {
          image: "../../static/image/cloth2.png",
          name:
            " J19329 XL",
          count: "2",
          size: "M",
          color: "",
          price: "100",
          active: "0.5",
          id: "6"
        },
        {
          image: "../../static/image/cloth2.png",
          name:
            " J19329 XL",
          count: "2",
          size: "M",
          color: "",
          price: "100",
          active: "0.5",
          id: "7"
        },
        {
          image: "../../static/image/cloth2.png",
          name:
            " J19329 XL",
          count: "2",
          size: "M",
          color: "",
          price: "100",
          active: "0.5",
          id: "8"
        },
        {
          image: "../../static/image/cloth2.png",
          name:
            " J19329 XL",
          count: "2",
          size: "M",
          color: "",
          price: "100",
          active: "0.5",
          id: "9"
        },
        {
          image: "../../static/image/cloth2.png",
          name:
            " J19329 XL",
          count: "2",
          size: "M",
          color: "",
          price: "100",
          active: "0.5",
          id: "10"
        },
        {
          image: "../../static/image/cloth2.png",
          name:
            " J19329 XL",
          count: "2",
          size: "M",
          color: "",
          price: "100",
          active: "0.5",
          id: "11"
        }
      ];
      arr.forEach(list => (list.checked = false));
      this.lists = arr;
    },
    addCount(list) {
      list.count++;
    },
    CutCount(list) {
      if (list.count === 0) return;
      list.count--;
    },
    delIcon(list) {
      let index = this.lists.findIndex(cloth => cloth.id == list.id);
      this.lists.splice(index, 1);
    },
    getMessage(data,Arr) {
      console.log("111111111111111-----------------------" + Arr.length);
      // console.log(allCheck);
      data = true;
      console.log(data+ Arr);
      this.allchecked = data;//
    //   this.seletedArrCloth =Array;
     
    }
  },
  components: {
    Footer
  }
};
</script>
<style>
* {
  margin: 0;
  padding: 0;
}

table td {
  border-collapse: collapse;
  /* width: 100%; */
  /* border: 3px solid red; */
}
table td .span {
  color: #6b6b6b;
  background-color: #e7eaed;
}
th {
  padding: 0px 10px;
}
td {
  padding: 10px 25px;
}

#Footer {
  position: relative;
  height: 50px;
  width: auto;
  bottom: 0;
}

.ClothBox {
  margin: 0px;
  padding: 0;
  height: 80vh;
}

.delIcon {
  width: 20px;
  height: 20px;
}
.span {
  display: block;
}
</style>



