<template>
    <div>
    
    
        <div class="footer">
    
            <input type="checkbox" @click="selectAll" @change="getAllChecked(allchecked)"/>
    
    
    
            <div class="checkAll"></div>
    
    
    
            <div class="container">
    
                <div class="totalPrice">{{totalPrice}}</div>
    
    
    
                <div class="clothCount">{{totalCount}}</div>
    
            </div>
    
        </div>
    
    </div>
</template>
<script>
export default {
    name: 'footer',
    props: ['lists', 'totalPrice', 'totalCount', 'seletedArrCloth', 'allchecked'],
    data() {
        return {
            seletedArr:[]
        }
    },
    methods: {
        selectAll() {
            this.$emit('funct', this.allchecked, this.seletedArr);
        },
        getAllChecked(allchecked){
            let seletedArrCloth1 = [];
            if(allchecked){
                this.lists.forEach(item => {
                    console.log(item.id);
                    seletedArrCloth1.push(item.id)
                })
                this.seletedArrCloth1 = seletedArrCloth1;
                console.log( this.seletedArrCloth1);
                this.seletedArr = this.seletedArrCloth1;
            }else{
                this.seletedArrCloth1 =[];
            }
        }
    }
}
</script>
<style scoped>
.footer {
    background-color: #545c64;
    display: flex;
    width: 100%;
    height: 60px;
}

.checkAll {
    display: inline-block;
    flex: 8;
}

.container {
    flex: 2;
}

.totalPrice {
    flex: 1;
    display: inline-block;
}

.clothCount {
    flex: 1;
    position: relative;
    display: inline-block;
    right: -40px;
}
</style>


